* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --black: #000000;
    --white: #ffffff;
    --purple: #9f9dba;
    --dark-purple: #7F678E;
    --bg-white: rgba(255, 255, 255, .7);
    --dark-blue: #0c0644;
}

body{
    font-family: "Montserrat", sans-serif;
    background-color:  var(--purple);
    color: var(--black);
}

.h1{
    font-weight: 500;
    font-size: 36px;
    line-height: 48px;
}
.h2{
    font-weight: 400;
    font-size: 32px;
    line-height: 39px;
}
.h3{
    font-weight: 400;
    font-size: 21px;
    line-height: 28px;
    margin-bottom: 0;
}

.container {
    max-width: 1920px;
    margin: 0 auto;
    padding: 0px 80px;
}

/* Header Block */
.header{
    width: 100%;
    height: auto;
    text-decoration: none;
    background-color: var(--dark-purple);
}
.header__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 9px 80px;
}

.header__link {
    text-decoration: none;
    color: var(--white);
    text-transform: uppercase;
}

.header__right {
    display: flex;
    justify-content: center;
    text-align: center;
}
.header__refresh{
    margin-right: 40px;
    display: flex;
    cursor: pointer;
}
.header__add-block, .header__filter, .header__search{
    background-color: var(--bg-white);
    border-radius: 11px;
    border: none;
    height: 38px;
}
.header__add-block{
    margin-right: 40px;
    width: 260px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header__add-movie{
    text-decoration: none;
    color: var(--black);
    margin-bottom: 0;
}

.header__search{
    display: flex;
    align-items: center;
}
.header__input{
    background-color: transparent;
    border: none;
    padding-left: 14px;
}
.header__input:focus{
    outline: none;
}
.header__search-btn {
    cursor: pointer;
}

/* End Header Block */
main{
    width: 100%;
    height: auto;
    margin-top: 60px;
}

.main__h2{
    margin-bottom: 60px;
}

.list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 40px;
    margin-bottom: 40px;
}

.movie{
    background-color: var(--bg-white);
    border-radius: 41px;
    width: 560px;
    text-align: center;
    word-wrap: break-word;
    min-height: 650px;
}
.movie__title{
    margin: 40px 0 5px;
    text-decoration: underline;
}
.movie__year {
    margin-bottom: 10px;
}
.rectangle{
    border: 1px solid var(--dark-blue);
    margin: 0 auto;
    width: 260px;
}
.movie__description{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 40px;
    margin-bottom: 15px;
    max-width: 100%;
    padding: 0 10px;
    min-height: 400px;
}

.movie__filters{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
}

.movie__status {
    margin-left: 10px;
    padding: 6px;
    border-radius: 11px;
    border: none;
}
.unwatched {
    background-color:  #f6685e;
}
.watched {
    background-color: #bbe166;
}

.movie__genre{
    text-align: center;
    background-color: var(--dark-purple);
    color: var(--white);
    width: 200px;
    max-width: 260px;
    margin-bottom: 15px;
    border-radius: 15px 0 0 15px;
    padding: 6px 0;
}

/* Filters block */


.filter-block {
    background-color: var(--dark-purple);
    padding: 15px;
    margin-bottom: 20px;
    border: 2px solid var(--black);
    border-radius: 13px;
}
.filter-form {
    display: none;
}
.filter-form.show {
    display: block;
}
.filter-checkbox{
    width: 20px;
    height: 20px;
}